import Vue from 'vue'
import Router from 'vue-router'
import Movie from '@/views/Movie'
import User from '@/views/User'

Vue.use(Router)

const routes = [
  {
    path: '/movie',
    name: 'movie',
    components: Movie
  },
  {
    path: '/user',
    name: 'user',
    components: User
  }
]

const router = new Router({
  mode: 'history',
  routes
})

export default router



