<template>
  <div id="app">
    <h1>main</h1>
    <div id="nav">
      <LoginForm />
      <SignupForm />
    </div>
    <router-view/>
  </div>
</template>

<script>
import LoginForm from '@/components/index/LoginForm'
import SignupForm from '@/components/index/SignupForm'

export default {
  name: 'App',
  components: {
    LoginForm,
    SignupForm
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

#nav {
  padding: 30px;
}

#nav a {
  font-weight: bold;
  color: #2c3e50;
}

#nav a.router-link-exact-active {
  color: #42b983;
}
</style>



